import React, { Component } from "react";
import Footer from "../Footer";
import Header from "../Header";

class PayFailed extends Component {
  render() {
    const { loggedInStatus, user, handleLogout } = this.props;

    return (
      <div>
        <Header loggedInStatus={loggedInStatus} user={user} isInner={true} handleLogout={handleLogout} />
        <section id="therapists">
          <div className="therapists-content pt-5 pb-2">
            <div className="container py-5 text-center">
            <img src="data:image/png;base64,"/>
              <div>
                <h2 className="py-4">Your Payment is failed.</h2>
                <p>Please check your card details.</p>
                <div className="form-group blue-blob pt-2 text-center">
                  <a
                    href="/mybookings"
                    type="button"
                    className="btn btn-primary green-btn"
                  >
                    Go My Bookings
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>
        <Footer />
      </div>
    );
  }
}
export default PayFailed;
